.Content {
  background-size: cover;
  background-image: url(../img//home3.jpg);
  width: 100%;
  height: 100vh;
}

.Bg_text {
  width: 100%;
  height: calc(100vh);
  z-index: 2;
}
.Maintext {
  position: absolute;
  top: 18%;
  left: 42%;
}
.Maintext p {
  color: darkblue;
  font-size: 30px;
  font-family: "Libre Baskerville", serif;
}
.text h2 {
  font-size: 30px;
  text-align: center;
  font-family: "Girassol", cursive;
  animation: text-color 15s infinite;
}
.text p {
  text-align: center;
  font-size: 30px;
  color: red;
  z-index: -2;
  padding: 0 5px;
}
.carousel_button {
  width: 130px;
  height: 50px;
  margin: auto;
  padding: 10px 0;
  transition: all 0.2s ease-in-out;
}
.carousel_button p {
  padding: 5px 0;
  text-align: center;

  font-size: 25px;
  color: rgb(16, 18, 150);
  border: 2px solid rgb(22, 29, 131);
  cursor: pointer;
  border-radius: 20px;
}
.carousel_button:hover {
  width: 150px;
  height: 55px;
  transform: scale(1.05);
}
@keyframes text-color {
  0% {
    bottom: -0.2em;
    opacity: 1;
    color: #5f9cd1;
  }
  25% {
    bottom: 0.2em;
    color: #1844bd;
  }
  50% {
    bottom: 0.2em;
    color: #0c1886;
  }

  100% {
    bottom: 0;
    opacity: 1;
    color: black;
  }
}
