ul {
  list-style-type: none;
  overflow: hidden;
  color: rgb(185, 202, 194);
}
li {
  float: right;
}
.navbarHover a:hover {
  color: rgb(82, 82, 79);
}
.navbarHover a {
  display: block;
  text-decoration: none;
  padding: 18px 16px;
  font-size: 20px;
  color: cornsilk;
  text-align: center;
}
.logo {
  float: left;
  text-align: center;
  font-size: 20px;
  padding: 18px 16px;
}
